import {
  createFormHook,
  formOptions,
  createFormHookContexts,
} from '@tanstack/react-form';
import { defaultLoanDetails } from '@/types/form';
import { unformatFormValues } from '@/utils/unformatFormValues';
import { useMortgage } from '@/providers/MortgageProvider';
import { useNavigate } from 'react-router-dom';
import { useLocalizedFormSchemas } from '@/schemas/localizedSchemas';
import { useEffect } from 'react';
import { cloudStorage } from '@telegram-apps/sdk-react';

export const { fieldContext, formContext, useFieldContext, useFormContext } =
  createFormHookContexts();

export const { useAppForm, withForm } = createFormHook({
  fieldContext,
  formContext,
  fieldComponents: {},
  formComponents: {},
});

export const formOpts = formOptions({
  defaultValues: defaultLoanDetails,
});

export const useLoanForm = () => {
  const navigate = useNavigate();
  // const [formData, setFormData] = useState<null | LoanDetailsType>(null);

  const { setLoanDetails, setEarlyPayments, setRegularPayments } =
    useMortgage();
  const { formSchema } = useLocalizedFormSchemas();

  useEffect(() => {
    const getKeys = async () => {
      console.log(cloudStorage.isSupported(), 'isSupported');

      try {
        if (cloudStorage.isSupported()) {
          console.log('1');
          const keys = await cloudStorage.getKeys();
          const test = await cloudStorage.getItem('form');
          console.log('2');
          alert(keys);
          alert(test);
        }
      } catch (error) {
        console.log(error, 'erorororor');
      }
    };

    getKeys();
  }, []);

  return useAppForm({
    ...formOpts,
    validators: {
      onChange: formSchema,
    },
    onSubmit: async ({ value }) => {
      try {
        const loanDetails = unformatFormValues(value);
        console.log(loanDetails, 'loanDetails');
        if (cloudStorage.isSupported()) {
          console.log('hi');
          cloudStorage.setItem('form', JSON.stringify(loanDetails));
          console.log('hi2');
        }
        const { earlyPayments, regularPayments, ...rest } = loanDetails;
        setLoanDetails(rest);
        setEarlyPayments(earlyPayments);
        setRegularPayments(regularPayments);
        navigate('result');
      } catch (error) {
        console.error('Form submission error:', error);
      }
    },
  });
};
